<template>
<div>
  <div v-show="nowTab === 1">
    <div class="crumbs">
      <el-breadcrumb separator="/">
        <el-breadcrumb-item><i class="el-icon-menu"></i></el-breadcrumb-item>
        <el-breadcrumb-item></el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <el-container>
      <el-header style="padding: 0px;display:flex;justify-content:space-between;align-items: center;margin-top:20px;">
        <div class="handle-box">
          <el-button type="primary" icon="plus" class="mr10" @click="toAddMouldRecord"></el-button>
          <el-input v-model="select_word" placeholder="" class="handle-input mr10" icon="search"></el-input>
        </div>
      </el-header>
      <el-main style="padding-left: 0px;padding-top: 0px">
        <el-table :data="data" stripe v-loading="tableLoading">
          <el-table-column prop="createTime" align="center" label="">
            <template slot-scope="scope">{{ scope.row.createTime | formatDateTime}}</template>
          </el-table-column>
          <el-table-column prop="batch" align="center" label="">
          </el-table-column>
          <el-table-column prop="clientCompanyName" align="center" label="">
          </el-table-column>
          <el-table-column align="center" label="">
            <template slot-scope="scope" v-if="scope.row.mouldInfos+'' !=='null' && scope.row.mouldInfos +'' !=='undefined'">{{ scope.row.mouldInfos.length}}</template>
          </el-table-column>
          <el-table-column prop="addUserName" align="center" label="">
          </el-table-column>
          <el-table-column align="center" label="">
            <template slot-scope="scope">
        <el-button
          size="mini"
          type="primary"
          @click="chaKan(scope.row)"></el-button>
          <el-button
            size="mini"
            type="warning"
            @click="mojuInfo(scope.row)"></el-button>
      </template>
          </el-table-column>
        </el-table>
      </el-main>
    </el-container>
    <div class="block" style="float:right;margin-top:15px;">
      <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="currentPage" :page-sizes="[10, 30, 80, 100]" :page-size="pagesize" layout="total,sizes, prev, pager, next, jumper" :total="totalnum">
      </el-pagination>
    </div>
  </div>
  <!-- card -->
  <div v-show="nowTab==3">
    <div class="table">
      <div class="crumbs">
        <el-breadcrumb separator="/">
          <el-breadcrumb-item><i class="el-icon-menu"></i></el-breadcrumb-item>
          <el-breadcrumb-item></el-breadcrumb-item>
        </el-breadcrumb>
      </div>
      <el-card class="box-card" style="margin-top:25px;">
        <div slot="header" class="clearfix">
          <span></span>
        </div>
        <div>
          <el-row :gutter="20">
            <el-col :span="6">
              <div @click="toChangeDisable">
                <el-card style="min-height:460px;" class="mould-card">
                  <div style="padding: 14px;">
                    <img class="image">
                    <i class="el-icon-plus"></i>
                  </div>
                </el-card>
              </div>
            </el-col>
            <el-col :span="6" v-for="part in partsInfos">
              <el-card :body-style="{ padding: '0px' }" class="mould-card" v-loading="mouldPartLoading">
                <img class="image" v-bind:src="part.picUrls[0]">
                <div style="padding: 14px;">
                  <h4>{{part.mouldName}}</h4>
                  <span>{{part.remark}}</span>
                  <div class="bottom clearfix">
                    <time class="time"></time>
                    <el-dropdown style="float: right;color: #409EFF;">
                      <span class="el-dropdown-link"><i class="el-icon-edit"></i>
                                      </span>
                      <el-dropdown-menu slot="dropdown">
                        <el-dropdown-item>
                          <el-button type="text" @click="mouldInfos(part)"></el-button>
                        </el-dropdown-item>
                        <el-dropdown-item>
                          <el-button type="text" @click="delMouldRecord(part.id)" :disabled="upadteDisabled"></el-button>
                        </el-dropdown-item>
                      </el-dropdown-menu>
                    </el-dropdown>
                  </div>
                </div>
              </el-card>
            </el-col>
          </el-row>
        </div>
        <div class="card-bottom">
          <el-button type="primary" style="float:left !important" @click="toLieBiao"></el-button>
        </div>
      </el-card>
    </div>
  </div>
  <!--  -->
  <div v-show="nowTab == 4">
    <div class="table">
      <div class="crumbs">
        <el-breadcrumb separator="/">
          <el-breadcrumb-item><i class="el-icon-menu"></i></el-breadcrumb-item>
          <el-breadcrumb-item></el-breadcrumb-item>
          <el-breadcrumb-item></el-breadcrumb-item>
        </el-breadcrumb>
      </div>
      <el-card class="box-card" style="margin-top:25px;">
        <div slot="header" class="clearfix">
          <span></span>
        </div>
        <div class="form-box">
          <el-form :model="form" ref="form" label-width="120px" :rules="rules">
            <el-form-item label="" prop="mouldName">
              <el-input style="width:28%" placeholder="" v-model="form.mouldName"></el-input>
            </el-form-item>
            <el-form-item label="" prop="mouldNum">
              <el-input-number v-model="form.mouldNum" controls-position="right" @change="handleChange" :min="1"></el-input-number>
            </el-form-item>
            <el-form-item label="" prop="remark">
              <el-input style="width:28%" type="textarea" v-model="form.remark"></el-input>
            </el-form-item>
            <el-form-item label="">
              <div>
                <vue-core-image-upload :crop="false" inputOfFile="imageFile" :url="upload"
                extensions="png,gif,jpeg,jpg"
                :class="['el-button', 'el-button--primary']"
                :max-file-size="5242880"
                :data="imageData"
                text=""
                :multiple="true" :multiple-size="30"
                credentials="true" @imageuploaded="imageuploaded" @errorhandle="handleError">
                </vue-core-image-upload>
              </div>
            </el-form-item>
          </el-form>
        </div>
        <div style="border: 1px dashed #d9d9d9;width:100%;min-height:358px;">
          <el-row :gutter="20" style="margin-top:10px;">
            <el-col :span="6" v-for="imageUrl in imageUrls ">
              <el-card :body-style="{ padding: '0px' }" class="mould-card">
                <img class="image" v-bind:src="imageUrl" style="height:420px;">
                <div style="text-align:center;">
                  <el-button type="text" @click="delMouldImage(imageUrl)"><i class="el-icon-error" style="color:red;"></i></el-button>
                </div>
              </el-card>
            </el-col>
          </el-row>
        </div>
        <div class="card-bottom">
          <el-button type="primary" style="float:left !important" @click="toMojuLieBiao"></el-button>
          <el-button v-show="part_disable === false" type="primary" @click="addMouldInfos('form')"></el-button>
          <el-button v-show="part_disable === true" type="primary" @click="to_update_partinfo('form')"></el-button>
        </div>
      </el-card>
    </div>
  </div>
  <!--  -->
  <div style="width:100%;float:right;" v-show="nowTab==2">
    <div class="table">
    <div class="crumbs">
      <el-breadcrumb separator="/">
        <el-breadcrumb-item><i class="el-icon-menu"></i></el-breadcrumb-item>
        <el-breadcrumb-item></el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <el-card class="box-card" style="margin-top:25px;">
      <div slot="header" class="clearfix">
        <span></span>
      </div>
      <div>
        <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="110px" class="demo-ruleForm">
          <el-form-item label="" prop="batch">
            <el-input style="width:28%;" placeholder="" v-model="batch" :disabled="true"></el-input>
          </el-form-item>
          <el-form-item label="" prop="createTime" required>
            <el-date-picker @change="toChange" :disabled="tiem_disablse" style="width:28%" format="yyyy  MM  dd  HH:mm:ss" value-format="yyyy-MM-dd HH:mm:ss" v-model="ruleForm.createTime" type="datetime" placeholder="">
            </el-date-picker>
          </el-form-item>
          <el-form-item label="" prop="clientCompanyName">
            <el-input style="width:28%;" placeholder="" v-model="ruleForm.clientCompanyName" :disabled="base_disablse"></el-input>
          </el-form-item>
          <el-form-item label="" prop="freightType">
            <el-radio-group v-model="ruleForm.freightType" :disabled="base_disablse">
              <el-radio label=""></el-radio>
              <el-radio label=""></el-radio>
              <el-radio label=""></el-radio>
            </el-radio-group>
          </el-form-item>
          <el-form-item label="" prop="carNum">
            <el-input style="width:28%;" placeholder="" v-model="ruleForm.carNum" :disabled="base_disablse"></el-input>
          </el-form-item>
          <el-form-item label="" prop="driverPhone">
            <el-input style="width:28%;" placeholder="" v-model="ruleForm.driverPhone" :disabled="base_disablse"></el-input>
          </el-form-item>
          <el-form-item label="">
            <div style="border: 1px dashed #d9d9d9;width:40%;min-height:128px;">
              <el-form-item label="" prop="jianBao">
                <el-switch v-model="ruleForm.jianBao" :disabled="base_disablse" active-text="" inactive-text="">
                </el-switch>
              </el-form-item>
              <el-form-item label="" style="margin-top:10px;" prop="woodenCaseNum">
                <el-input-number v-model="ruleForm.woodenCaseNum" controls-position="right" @change="handleChange" :min="0" :disabled="base_disablse"></el-input-number>
              </el-form-item>
              <el-form-item label="" style="margin-top:10px;" prop="palletNum">
                <el-input-number v-model="ruleForm.palletNum" controls-position="right" @change="handleChange" :min="0" :disabled="base_disablse"></el-input-number>
              </el-form-item>
              <el-form-item label="" style="margin-top:10px;" prop="diaoHuanNum">
                <el-input-number v-model="ruleForm.diaoHuanNum" controls-position="right" @change="handleChange" :min="0" :disabled="base_disablse"></el-input-number>
              </el-form-item>
            </div>
          </el-form-item>
        </el-form>
      </div>
      <div>
        <div class="card-bottom">
          <el-button type="primary" style="float:left" @click="goBack">  </el-button>
          <el-button style="float:right" v-show="toAddDisable === true" @click="resetForm('ruleForm')"> </el-button>
          <el-button type="primary" style="float:right" @click="submitForm('ruleForm')" v-show="toAddDisable === true"> </el-button>
          <el-button style="float:right" v-show="update_disablse === true " type="primary" @click="updateCollectionInfo('ruleForm')"> </el-button>
        </div>
      </div>
    </el-card>
  </div>
  </div>
</div>
</template>
<script>
import timeFilter from '../../utils/timeFilter.js'
import VueCoreImageUpload from 'vue-core-image-upload';
export default {
  components: {
    VueCoreImageUpload
  },
  data() {
    return {
      pkGuid: '',
      shouMoId: '',
      mouldPartLoading: false,
      updateId: '',
      partsInfos: [],
      nowTab: 1,
      mojuCard: false,
      moJuDisable: false,
      form: {
        mouldNum: 1,
        addUserId: '',
        addUserName: '',
        shouMoId: '',
        picUrls: ''
      },
      upload: 'http://39.107.78.95:8082/config/upload/image',
      // upload: 'http://localhost:8885/config/upload/image',
      imageData: {
        userId: '',
        imgPakage: 'sfm_shou'
      },
      imageUrls: [],
      // 
      tableLoading: false,
      changeDisable: true,
      toAddDisable: false,
      currentPage: 1,
      totalnum: 0,
      pagesize: 10,
      select_word: '',
      tableData: [],
      username: '',
      uid: '',
      batch: '',
      upadteDisabled: false,
      tiem_disablse: false,
      part_disable: false,
      chomgzi_dis: false,
      update_disablse: false,
      base_disablse: false,
      doc_collect_id: '',
      dialogFormVisible: false,
      itemForm: {},
      ruleForm: {
        createTime: '',
        addUserName: '',
        addUserId: '',
        woodenCaseNum: '0',
        palletNum: '0',
        diaoHuanNum: '0',
        clientCompanyName: '',
        freightType: '',
        carNum: '',
        jianBao: false,
        driverPhone: ''
      },
      rules: {
        clientCompanyName: [{
          min: 1,
          max: 64,
          message: ' 1  64 ',
          trigger: 'blur'
        }],
        freightType: [{
          required: true,
          message: '',
          trigger: 'blur'
        }],
        carNum: [{
          min: 8,
          max: 24,
          message: ' 8  24 ',
          trigger: 'blur'
        }],
        driverPhone: [{
          min: 11,
          max: 11,
          message: '',
          trigger: 'blur'
        }],
        remark: [{
          min: 0,
          max: 255,
          message: ' 0  255 ',
          trigger: 'blur'
        }],
        mouldName: [{
            required: true,
            message: '',
            trigger: 'blur'
          },
          {
            min: 1,
            max: 255,
            message: ' 1  255 ',
            trigger: 'blur'
          }
        ]
      }
    }
  },
  methods: {
    delMouldRecord(pkGuid) {
        this.$confirm(', ?', '', {
            confirmButtonText: '',
            cancelButtonText: '',
            type: 'warning'
        }).then(() => {
            this.toDelPartInfo(pkGuid);
        }).catch(() => {
            this.$message({
                type: 'info',
                message: ''
            });
        });
    },
    toDelPartInfo(pkGuid) {
        let self = this;
        if(pkGuid == 'undefined' || pkGuid == ''){
          self.$message.error("")
          return
        }
        this.getRequest("/shou/mo/del/mould?mouldId="+pkGuid).then(resp => {
            if (resp && resp.status == 200 && resp.data.code == 0) {
                this.toGetColletPartInfo();
                self.$message.success("");
            } else {
                self.$message.error("");
            }
        })
    },
    toGetColletPartInfo() {
      this.mouldPartLoading = true;
      let self = this;
      if (self.pkGuid == 'undefined' || self.pkGuid == '') {
        this.mouldPartLoading = false;
        self.$message.error("")
        return
      }
      this.getRequest("/shou/mo/list/mouldinfo?shouMoId="+self.pkGuid).then(resp => {
        this.mouldPartLoading = false;
        if (resp && resp.status == 200 && resp.data.code == 0) {
          this.partsInfos = [];
          let parts = resp.data.data;
          parts.forEach(function(part, index, array) {
            let picurl = part.picUrls;
            let urlArray = [];
            if (picurl != "undefined" && picurl != "") {
              urlArray = picurl.split('|');
              part.picUrls = urlArray;
            }
            self.partsInfos.push(part);
          })
        } else {
          console.log("");
        }
      });
    },
    to_update_partinfo(formName) {
      let self = this;
      this.$refs[formName].validate((valid) => {
        if (valid) {
          let paramsData = self.form;
          if (self.imageUrls.length == 0) {
            self.$message.error("");
            return;
          }
          let url = "";
          for (var i = 0; i < self.imageUrls.length; i++) {
            if (i == self.imageUrls.length - 1) {
              url = url + self.imageUrls[i]
            } else {
              url = self.imageUrls[i] + "|" + url
            }
          }
          paramsData.picUrls = url
          paramsData.shouMoId = this.shouMoId;
          paramsData.id = this.updateId;
          this.postRequest("/shou/mo/update/mouldinfo", paramsData).then(resp => {
            if (resp && resp.status == 200 && resp.data.code == 0) {
              self.$message.success("");
              self.resetForm('form');
              self.imageUrls = [];
              this.nowTab = 3;
              this.toGetColletPartInfo();
            } else {
              self.$message.error('');
            }
          })
        } else {
          console.log('');
          return false;
        }
      });
    },
    mouldInfos(item) {
      this.nowTab = 4;
      this.form = item;
      this.imageUrls = item.picUrls;
      this.part_disable = true;
      this.shouMoId = item.shouMoId;
      this.updateId = item.id;
    },
    toMojuLieBiao() {
      this.nowTab = 3;
    },
    toLieBiao() {
      this.nowTab = 1;
    },
    mojuInfo(item) {
      this.nowTab = 3;
      this.pkGuid = item.id;
      let parts = []
      parts = item.mouldInfos;
      if (parts != 'undefined' && parts.length > 0) {
        let newPartInfo = [];
        parts.forEach(function(part, index, array) {
          let picurl = part.picUrls;
          let urlArray = [];
          if (picurl != "undefined" && picurl != "") {
            urlArray = picurl.split('|');
            part.picUrls = urlArray;
          }
          newPartInfo.push(part);
        })
        this.partsInfos = newPartInfo;
      }
    },
    // 
    addMouldInfos(formName) {
      let self = this;
      this.$refs[formName].validate((valid) => {
        if (valid) {
          let paramsData = self.form;
          if (self.imageUrls.length == 0) {
            self.$message.error("");
            return;
          }
          let url = "";
          for (var i = 0; i < self.imageUrls.length; i++) {
            if (i == self.imageUrls.length - 1) {
              url = url + self.imageUrls[i]
            } else {
              url = self.imageUrls[i] + "|" + url
            }
          }
          paramsData.picUrls = url
          paramsData.addUserId = this.uid;
          paramsData.addUserName = this.name;
          paramsData.shouMoId = self.pkGuid;
          this.postRequest("/shou/mo/create/mouldinfo", paramsData).then(resp => {
            if (resp && resp.status == 200 && resp.data.code == 0) {
              self.$message.success("");
              self.resetForm('form');
              self.imageUrls = [];
              self.nowTab = 1;
              self.getCollectMouldList();
              // self.dialogFormVisible = true;
              // self.mould_id = data.data.pkGuid;
              // self.partsInfos=[];
              // self.toGetColletPartInfo(self.doc_collect_id);
            } else {
              self.$message.error('');
            }
          })
        } else {
          console.log('');
          return false;
        }
      });
    },
    imageuploaded(data) {
      let self = this;
      if (data.url.length !== 'undefined' && data.url.length > 0) {
        let url = data.url
        let urls = self.imageUrls;
        for (var i = 0; i < url.length; i++) {
          urls.push(url[i]);
        }
        self.imageUrls = urls;
      }
    },
    // 
    handleError() {
      this.$notify.error({
        title: '',
        message: ''
      });
    },
    todelImage(url) {
      let self = this
      if (url == 'undefined' || url.length == "") {
        self.$message.error("");
        return
      }
      this.getRequest("/config/delfile/delImage?url=" + url).then(resp => {
        if (resp && resp.status == 200 && resp.data.code == 0) {
          self.$message.success("");
          let urls = self.imageUrls;
          let newUrls = [];
          for (var i = 0; i < urls.length; i++) {
            if (url != urls[i]) {
              newUrls.push(urls[i])
            }
          }
          self.imageUrls = newUrls;
        } else {
          self.$message.error("");
        }
      })

    },
    delMouldImage(url) {
      this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        this.todelImage(url);
      }).catch(() => {
        this.$message({
          type: 'info',
          message: ''
        });
      });
    },
    goBack() {
      this.nowTab = 1;
      this.toAddDisable = false;
      this.update_disablse = false;
      this.tiem_disablse = false;
    },
    toAddMouldRecord() {
      this.nowTab = 2;
      this.toAddDisable = !this.toAddDisable;
      this.tiem_disablse = false;
      this.ruleForm = {
        createTime: '',
        addUserName: '',
        addUserId: '',
        woodenCaseNum: '0',
        palletNum: '0',
        diaoHuanNum: '0',
        clientCompanyName: '',
        freightType: '',
        carNum: '',
        jianBao: false,
        driverPhone: ''
      };
      this.batch = "";
    },
    chaKan(item) {
      let self = this;
      self.nowTab = 2;
      // let time = timeFilter.formatTime(item.createTime);
      // let updatetime = timeFilter.formatTime(item.updateTime);
      // item.createTime = time;
      // item.updateTime = updatetime;
      self.tiem_disablse = true;
      self.update_disablse = true;
      self.ruleForm = item;
      self.batch = item.batch;
    },
    // 
    updateCollectionInfo(formName) {
      let self = this
      this.$refs[formName].validate((valid) => {
        if (valid) {
          if (self.ruleForm.id == "undefined" || self.ruleForm.id + '' == "") {
            self.$message.error("");
            return;
          }
          this.putRequest("/shou/mo/update/baseinfo", self.ruleForm).then(resp => {
            if (resp && resp.status == 200 && resp.data.code == 0) {
              self.$message.success("");
              self.goBack();
              self.getCollectMouldList();
            } else {
              self.$message.error('');
            }
          })
        } else {
          return false;
        }
      });
    },
    // 
    resetForm(formName) {
      this.$refs[formName].resetFields();
      if(formName == 'ruleForm') {
        this.ruleForm.createTime="";
        this.batch="";
      }
    },
    // 
    submitForm(formName) {
      let self = this
      this.$refs[formName].validate((valid) => {
        if (valid) {
          self.ruleForm.addUserId = this.uid;
          self.ruleForm.addUserName = this.name;
          self.ruleForm.batch = this.batch
          this.postRequest("/shou/mo/add/baseinfo", self.ruleForm).then(resp => {
            if (resp && resp.status == 200 && resp.data.code == 0) {
              var id = resp.data.data.id;
              if (id == -1) {
                _this.$message.error("");
              } else {
                // 
                self.doc_collect_id = id;
                self.nowTab = 1;
                this.resetForm('ruleForm')
                this.tableLoading = true;
                self.getCollectMouldList();
                self.$message.success("");
              }
            }
          })
        } else {
          self.$message.error("");
          return false;
        }
      });
    },
    toChange() {
      this.toInitBatch();
    },
    toInitBatch() {
      let self = this;
      let time = self.ruleForm.createTime;
      if (time == '' || time == 'undefined') {
        self.$message.error('');
        return;
      }
      this.getRequest("/shou/mo/max/batch?dateTime=" + time).then(resp => {
        if (resp && resp.status == 200 && resp.data.code == 0) {
          let maxBatch = resp.data.data + 1;
          self.$nextTick(function() {
            this.batch = "" + maxBatch + "";
          })
        } else {
          self.ruleForm.batch = '';
        }
      })
    },
    toChangeDisable() {
      this.nowTab = 4;
    },
    toUpdate() {
      this.update_disablse = true;
    },
    handleChange(value) {},

    handleSizeChange(val) {
      this.pagesize = val;
      this.getCollectMouldList();
    },
    handleCurrentChange(val) {
      this.currentPage = val;
      this.getCollectMouldList();
    },
    // 
    getCollectMouldList() {
      let _this = this
      this.getRequest("/shou/mo/listbypage?page=" + this.currentPage + "&size=" + this.pagesize).then(resp => {
        this.tableLoading = false;
        if (resp && resp.status == 200 && resp.data.code==0){
          _this.tableData = resp.data.data.shoumolist
          _this.totalnum = resp.data.data.count
        }
      })
    }
  },
  mounted: function() {
    this.$nextTick(function() {
      this.imageData.userId = localStorage.getItem('cp_uid');
      this.tableLoading = true;
      this.getCollectMouldList();
    })
    this.username = localStorage.getItem('cp_username');
    this.uid = localStorage.getItem('cp_uid');
    this.name = localStorage.getItem('cp_name');

  },
  computed: {
    data() {
      const self = this;
      return self.tableData.filter(function(d) {
        if (d.clientCompanyName.indexOf(self.select_word) > -1 ||
          d.addUserName.indexOf(self.select_word) > -1
        ) {
          return d;
        }
      });
    }
  }
}
</script>
<style scoped>
.handle-box {
  margin-bottom: 20px;
}

.card-bottom {
  padding: 18px 20px;
  border-top: 1px solid #e6ebf5;
  box-sizing: border-box;
  text-align: right;
}

.card-bottom-left {
  padding: 18px 20px;
  border-top: 1px solid #e6ebf5;
  box-sizing: border-box;
  text-align: left;
}

.handle-input {
  width: 300px;
  display: inline-block;
}

.mould-card {
  margin-bottom: 15px;
}

.mould-card:hover {
  transform: translateY(-2%);
  box-shadow: 1px 4px 10px 2px #CCC;
}

.time {
  font-size: 13px;
  color: #999;
}

.bottom {
  margin-top: 13px;
  line-height: 12px;
}

.button {
  padding: 0;
  float: right;
}

.image {
  width: 100%;
  display: block;
}

.pre-img {
  width: 250px;
  height: 250px;
  margin-bottom: 20px;
}
</style>



